#root {
  height: 100vh;
  position: relative;
}

#title {
  font-size: 30px;
  display: flex;
  justify-content: center;
}

#message_box {
  height: 80vh;
  width: 85%;
  overflow: auto;
  display: inline-block;
  font-size: 10px;
  border: solid 1px black;
}

.red_title {
  color: #B91919;
}

.green_title {
  color: #7CFC00;
}

#message_box ul {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
}

#userlist {
  height: 80vh;
  width: 15%;
  overflow: auto;
  display: inline-block;
  font-size: 10px;
  border: solid 1px black;
}

#userlist ul {
  list-style-type: none; /* Remove bullets */
  padding: 0; /* Remove padding */
  margin: 0; /* Remove margins */
  text-align: center;
}

#compose {
  width: 100%;
}

#compose input {
  height: 5vh;
}

.overlay {
  position: fixed; /* Positioning and size */
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(128,128,128,0.5); /* color */
  display: none; /* making it hidden by default */
}
